/* src/index.css */
@tailwind base;
@tailwind components;
@tailwind utilities;

/* تنظیمات فونت فارسی */
@import url('https://cdn.jsdelivr.net/gh/rastikerdar/vazirmatn@v33.003/Vazirmatn-font-face.css');

/* تنظیمات پایه */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: 'Vazirmatn', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  direction: rtl;
  scroll-behavior: smooth;
}

body {
  font-family: 'Vazirmatn', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  direction: rtl;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

/* تنظیمات Scrollbar */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background: rgba(156, 163, 175, 0.5);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: rgba(156, 163, 175, 0.7);
}

.dark ::-webkit-scrollbar-thumb {
  background: rgba(75, 85, 99, 0.5);
}

.dark ::-webkit-scrollbar-thumb:hover {
  background: rgba(75, 85, 99, 0.7);
}

/* Glassmorphism Effects */
.glass-light {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.glass-dark {
  background: rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

/* Custom Animations */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideInRight {
  from {
    opacity: 0;
    transform: translateX(-30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: .8;
  }
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes bounce {
  0%, 100% {
    transform: translateY(-25%);
    animation-timing-function: cubic-bezier(0.8, 0, 1, 1);
  }
  50% {
    transform: translateY(0);
    animation-timing-function: cubic-bezier(0, 0, 0.2, 1);
  }
}

/* Utility Classes */
.animate-fade-in-up {
  animation: fadeInUp 0.5s ease-out;
}

.animate-slide-in-right {
  animation: slideInRight 0.3s ease-out;
}

.animate-pulse-slow {
  animation: pulse 3s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}

/* Focus States */
.focus-ring {
  @apply focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2;
}

.dark .focus-ring {
  @apply focus:ring-offset-gray-800;
}

/* Button Variants */
.btn-primary {
  @apply bg-gradient-to-r from-blue-500 to-blue-600 text-white font-medium px-4 py-2 rounded-lg 
         shadow-lg hover:shadow-xl transform hover:scale-105 transition-all duration-200 
         focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2;
}

.btn-secondary {
  @apply bg-gray-200 dark:bg-gray-700 text-gray-900 dark:text-white font-medium px-4 py-2 rounded-lg 
         hover:bg-gray-300 dark:hover:bg-gray-600 transition-colors duration-200 
         focus:outline-none focus:ring-2 focus:ring-gray-500 focus:ring-offset-2;
}

.btn-ghost {
  @apply text-gray-600 dark:text-gray-300 font-medium px-4 py-2 rounded-lg 
         hover:bg-gray-100 dark:hover:bg-gray-700 transition-colors duration-200 
         focus:outline-none focus:ring-2 focus:ring-gray-500 focus:ring-offset-2;
}

/* Card Variants */
.card {
  @apply rounded-xl p-6 backdrop-blur-xl border transition-all duration-200 
         bg-white/30 dark:bg-gray-800/30 border-white/20 dark:border-gray-700/20 
         hover:shadow-xl;
}

.card-glass {
  @apply rounded-xl p-6 backdrop-blur-xl border 
         bg-white/10 dark:bg-gray-800/10 border-white/20 dark:border-gray-700/20;
}

/* Input Styles */
.input-field {
  @apply w-full px-4 py-3 rounded-lg border 
         bg-white/50 dark:bg-gray-800/50 border-gray-200/30 dark:border-gray-700/30 
         text-gray-900 dark:text-white placeholder-gray-500 dark:placeholder-gray-400 
         focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent 
         transition-all duration-200;
}

/* Text Utilities */
.text-gradient {
  @apply bg-gradient-to-r from-blue-600 to-purple-600 bg-clip-text text-transparent;
}

.heading-1 {
  @apply text-4xl lg:text-5xl font-bold text-gray-900 dark:text-white;
}

.heading-2 {
  @apply text-3xl lg:text-4xl font-bold text-gray-900 dark:text-white;
}

.heading-3 {
  @apply text-2xl lg:text-3xl font-bold text-gray-900 dark:text-white;
}

.body-large {
  @apply text-lg text-gray-600 dark:text-gray-300;
}

.body-text {
  @apply text-base text-gray-600 dark:text-gray-300;
}

.body-small {
  @apply text-sm text-gray-500 dark:text-gray-400;
}

/* Layout Utilities */
.container-fluid {
  @apply max-w-7xl mx-auto px-4 sm:px-6 lg:px-8;
}

.section-padding {
  @apply py-12 lg:py-16;
}

.grid-auto-fit {
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
}

.grid-auto-fill {
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
}

/* Status Indicators */
.status-success {
  @apply bg-green-100 dark:bg-green-900/20 text-green-800 dark:text-green-400 
         border border-green-200 dark:border-green-800 px-3 py-1 rounded-full text-sm font-medium;
}

.status-warning {
  @apply bg-yellow-100 dark:bg-yellow-900/20 text-yellow-800 dark:text-yellow-400 
         border border-yellow-200 dark:border-yellow-800 px-3 py-1 rounded-full text-sm font-medium;
}

.status-error {
  @apply bg-red-100 dark:bg-red-900/20 text-red-800 dark:text-red-400 
         border border-red-200 dark:border-red-800 px-3 py-1 rounded-full text-sm font-medium;
}

.status-info {
  @apply bg-blue-100 dark:bg-blue-900/20 text-blue-800 dark:text-blue-400 
         border border-blue-200 dark:border-blue-800 px-3 py-1 rounded-full text-sm font-medium;
}

/* Loading States */
.skeleton {
  @apply bg-gray-200 dark:bg-gray-700 animate-pulse rounded;
}

.loading-spinner {
  @apply inline-block w-4 h-4 border-2 border-current border-r-transparent rounded-full animate-spin;
}

/* Responsive Typography */
@media (max-width: 640px) {
  .heading-1 {
    @apply text-2xl;
  }
  
  .heading-2 {
    @apply text-xl;
  }
  
  .heading-3 {
    @apply text-lg;
  }
}

/* Print Styles */
@media print {
  .no-print {
    display: none !important;
  }
  
  .print-only {
    display: block !important;
  }
  
  body {
    background: white !important;
    color: black !important;
  }
}

/* High Contrast Mode */
@media (prefers-contrast: high) {
  .card {
    @apply border-2 border-gray-400 dark:border-gray-600;
  }
  
  .btn-primary {
    @apply border-2 border-blue-700;
  }
}

/* Reduced Motion */
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
}

/* RTL Specific Styles */
[dir="rtl"] .transform {
  transform: scaleX(-1);
}

[dir="rtl"] .rotate-180 {
  transform: rotate(180deg) scaleX(-1);
}

/* Custom Selection */
::selection {
  background-color: rgba(59, 130, 246, 0.3);
  color: inherit;
}

::-moz-selection {
  background-color: rgba(59, 130, 246, 0.3);
  color: inherit;
}

/* Remove default button styles */
button {
  background: none;
  border: none;
  cursor: pointer;
}

button:disabled {
  cursor: not-allowed;
  opacity: 0.6;
}

/* Remove input number arrows */
input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"] {
  -moz-appearance: textfield;
}

/* Custom File Input */
.file-input {
  @apply relative cursor-pointer bg-white dark:bg-gray-800 border border-gray-300 dark:border-gray-600 
         rounded-lg px-4 py-3 text-sm text-gray-700 dark:text-gray-300 
         hover:bg-gray-50 dark:hover:bg-gray-700 transition-colors duration-200;
}

.file-input input[type="file"] {
  @apply absolute inset-0 w-full h-full opacity-0 cursor-pointer;
}